import * as types from '../mutation-types'

// initial state
const state = {
  all: [],
  activity_types:[],//活动类型数据
  load: false,  //子模块的状态控制 state.notes.load 来获取到
  activity_cut: {
    title: '标题',
    banner: [{
      //url: 'javascript:',
      img: 'https://static.vux.li/demo/1.jpg',
      //title: '送你一朵fua'
    }, {
      //url: 'javascript:',
      img: 'https://static.vux.li/demo/2.jpg',
      title: '送你一辆车'
    }, {
      url: 'javascript:',
      img: 'https://static.vux.li/demo/3.jpg',
      //title: '送你一次旅行'
    }],//轮播图片,
    activity_type: 1,
    begin_time: '', //活动开启时间
    end_time: '',   //活动结束时间
    original_price: '', //原件
    floor_price: '',    //底价
    cut_price_low: '',
    cut_price_high: '',
    interval: '',
    num: 0,    //限定商品数量0不限制
    surplus_num:0,//剩余商品数量
    describe: '',//商品描述
    imgs: [],
    rules: '',//活动规则
    award_msg: '',
    org_msg: '',
    companyImgs: [],
    phone: '',
    activity_type:1,
    cut_num:0,
    allow_apply_num:0,


  },
  activity_group_buy: {

  }
}
// getters
const getters = {
  //allProducts: state => state.all
}

// actions
const actions = {
  getAllProducts ({ commit }) {thread
    commit(types.RECEIVE_PRODUCTS)
  }
}

// mutations
const mutations = {
  [types.RECEIVE_PRODUCTS] (state, { products }) {
    state.all = products
  },

  [types.ADD_TO_CART] (state, { id }) {
    state.all.find(p => p.id === id).inventory--
  },
  increment (state) {
    console.log('increment notes');
  },
  increment1 (state) {
    // 变更状态
    console.log('increment1')
  },
  increment2 (state) {
    // 变更状态
    console.log('increment2')
  }

}

export default {
  state: state,
  getters: getters,
  actions: actions,
  mutations: mutations
}



