import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/channel/ChannelHome.vue'
import Auth from '../views/channel/Auth.vue'
import ActivityTypes from '../views/channel/ActivityTypes.vue'
import MakeGroupBuyActivity from '../views/channel/MakeGroupBuyActivity.vue'
import MakeCutActivity from '../views/channel/MakeCutActivity.vue'
import CutActivity from '../views/CutActivity.vue'
import GroupBuyActivity from '../views/GroupBuyActivity.vue'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    { path: '/channel/home', name: 'Home', component: Home },
    { path: '/channel/auth', name: 'auth', component: Auth },
    { path: '/channel/act/m_cut', name: 'act_m_cut', component: MakeCutActivity },
    { path: '/channel/act/m_group_buy', name: 'act_m_group_buy', component: MakeGroupBuyActivity },
    { path: '/act/cut', name: 'act_cut', component: CutActivity },
    { path: '/act/types', name: 'act_types', component: ActivityTypes },
    { path: '/act/group_buy', name: 'act_group_buy', component: GroupBuyActivity },
    { path: '/', redirect: '/channel/home' }
  ]
})



